import React from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Button } from "react-bootstrap";

import { SponsorProps, sponsors } from "../general/sponsors";

import * as styles from "./sponsors.module.css";

type TierProps = {
  title: string;
  tierSponsors: SponsorProps[];
};

const Tier: React.FC<TierProps> = ({ title, tierSponsors }) => {
  return (
    <>
      <h2 className={"title"}>{title}</h2>
      <div className={styles.sponsorList}>
        {tierSponsors.map((item, index) => (
          <a
            key={title + index}
            className={styles.sponsor}
            target={"_blank"}
            rel={"noreferrer"}
            href={item.link}
          >
            <img
              src={item.image}
              alt={item.name}
              className={styles.sponsorImage}
            />
          </a>
        ))}
      </div>
    </>
  );
};

const Sponsors: React.FC = () => {
  const { t } = useTranslation();

  const goldSponsors = sponsors.filter((sponsor) => sponsor.pledge > 500);
  const silverSponsors = sponsors.filter(
    (sponsor) => sponsor.pledge > 0 && sponsor.pledge <= 500,
  );
  const partners = sponsors.filter((sponsor) => sponsor.pledge === 0);

  return (
    <div className={`page ${styles.sponsors}`} id={"sponsors"}>
      <Tier title={t("goldSponsors")} tierSponsors={goldSponsors} />
      <Tier title={t("silverSponsors")} tierSponsors={silverSponsors} />
      <Tier title={t("partners")} tierSponsors={partners} />

      <p>{t("contactDetails")}</p>
      <Button
        className={"buttonLink"}
        target={"_blank"}
        rel={"noreferrer"}
        href={"mailto:contact@nitronlp.rocks"}
      >
        {t("contactButton")}
      </Button>
    </div>
  );
};

export default Sponsors;
